export default {
	data() {
		return {
			lists:'',
			biId:'',
			authKey:'',
			level:'',
			count:'',
			isaa:3,
			loadingType: 0,
			next : -1,
			start: 0,
			size: 15,
			record: false,
			bottom: false,
		};
	},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey');
		this.biId = e.id;
		this.getListComments();
		this.getListComment(3);
	},
	onShow() {
		this.start = 0
	},
	methods:{
		// 评价个数
		getListComments(){
			var that = this;
			uni.request({
				url: this.http+'comment/overview/' + this.biId,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						this.count = res.data.data.count
					}
				},
			});
		},
		evala(e){
			this.isaa = e
			this.start = 0
			this.getListComment(e);
		},
		// 评价列表
		getListComment: function(e,isLoading = true){
			var level = e
			console.log(e)
			var that = this;
			let querys = "?start=" + this.start + "&size=" + this.size
			if(level == 3){
				uni.request({
					url: this.http+'comment/list/' + querys,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
						id:this.biId
					},
					success: res => {
						let data = res.data.data
						if (res.data.code == 200) {
							let order = data.list
							if (order.length == 0) {
								that.loadingType = 2
								order = []
							}
							else if (order.length < that.size)
								that.loadingType = 2
							else 
								that.loadingType = 0
							
							if (that.start == 0) 
								that.lists = []
						   
							that.lists = that.lists.concat(order)
							if (that.lists.length == 0) 
								that.record = true
							else 
								that.record = false
							 that.start = data.next	
						}
					},
				});
			}else{
				uni.request({
					url: this.http+'comment/list/' + querys,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
						id:this.biId,
						level:level
					},
					success: res => {
						let data = res.data.data
						if (res.data.code == 200) {
							let order = data.list
							if (order.length == 0) {
								that.loadingType = 2
								order = []
							}
							else if (order.length < that.size)
								that.loadingType = 2
							else 
								that.loadingType = 0
							
							if (that.start == 0) 
								that.lists = []
						   
							that.lists = that.lists.concat(order)
							if (that.lists.length == 0) 
								that.record = true
							else 
								that.record = false
							 that.start = data.next	
						}
					},
				});
			}
		},
		lower(){
			if (this.loadingType == 2) {
				return
			}
			this.getListComment(false)
		}
	}
}
